html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "ABeeZee", Georgia, 'Arial';
  background: url(../assets/img/drawing-pad-1209781_1920.jpg) no-repeat fixed bottom center;
  background-size: cover;
  height: 100vh;
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center; }

.main__header {
  margin: 0;
  padding: 10px 0;
  font-family: 'Cabin Sketch', Georgia, 'Arial';
  color: #515457;
  text-align: center;
  width: 100%;
  background: rgba(253, 253, 253, 0.678);
  font-size: 4rem;
  position: absolute;
  top: 3.5%;
  left: 0;
  right: 0; }

.main__content {
  min-width: 300px;
  width: 60vw;
  height: 60vh;
  margin: 5% 0 0;
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.form {
  margin-bottom: 16px; }
  .form__textarea {
    width: 100%;
    height: 50vh;
    padding: 10px;
    background: none;
    resize: vertical;
    position: relative;
    border-radius: 4px;
    font-size: large; }

.button {
  display: flex;
  justify-content: flex-end; }
  .button__load {
    max-width: 200px;
    width: 100%;
    height: 48px;
    border-radius: 4px;
    border: none;
    color: #fff;
    cursor: pointer;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.247);
    transition: transform .3s ease-in-out;
    margin-right: 14px;
    background: #8C756A; }
    .button__load:hover {
      transform: scale(1.05);
      transition: transform .3s ease-in-out; }
  .button__save {
    max-width: 200px;
    width: 100%;
    height: 48px;
    border-radius: 4px;
    border: none;
    color: #fff;
    cursor: pointer;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.247);
    transition: transform .3s ease-in-out;
    background: linear-gradient(180deg, #FEB06A 0%, #FEB06A 100%);
    position: relative; }
    .button__save:hover {
      transform: scale(1.05);
      transition: transform .3s ease-in-out; }
    .button__save--showInfo::after {
      content: 'Note saved.';
      position: absolute;
      width: 300px;
      height: 50px;
      bottom: 0;
      text-align: center;
      padding: 15px;
      background: rgba(255, 255, 255, 0.856);
      animation: opacity 5s ease-in-out; }

@keyframes opacity {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@media (max-width: 640px), (max-height: 340px) {
  .main__header {
    font-size: 3rem; }
  .button__load, .button__save {
    max-width: 100px;
    height: 34px; } }

@media (max-width: 360px) {
  .main__header {
    font-size: 2.5rem; } }
